    m.changeLang = function (lang, e, elem) {
      m.docCookies.setItem("lang", lang, Infinity, "/");
      let search = window.location.search;
      let searchVars = m.getSearchVars(search);
      if (searchVars["lang"]) {
        searchVars["lang"].val = dataLang[0][i];
      }
      else {
        searchVars["lang"] = { key: "lang", val: dataLang[0][i] };
        searchVars.push(searchVars["lang"]);
      }
      let href = `${window.location.pathname}${m.arrayToSearch(searchVars)}#${encodeURIComponent(m.hashURI)}`;
      if (e?.which === 2) {
        // wheel click.
        $(elem).attr({ href: href });
        return true;
      }
      else {
        window.location.href = href;
        return false;
      }
    };

    (async function () {
      let dataLang = Object(await m.strToJSON(m.$data_lang.html().trim()));
      let htmlLangs = "";
      for (let i = 0; i < dataLang[0].length; i++) {
        htmlLangs += `<a href="${window.location.pathname}?lang=${dataLang[0][i]}" onclick="return m.changeLang('${dataLang[0][i]}', event, this)">${dataLang[1][i]}</a><br/>`;
      }
      m.$choose_lang_list.prepend(htmlLangs);
      m.$choose_lang.on("click", function (e) {
        if (m.$choose_lang.hasClass("enabled")) {
          m.$choose_lang.removeClass("enabled");
          m.$choose_lang_list.hide();
        }
        else {
          m.$choose_lang.addClass("enabled");
          m.$choose_lang_list.show();
        }
      });
      m.$choose_timezone.on("click", function (e) {
        if (m.$choose_timezone.hasClass("enabled")) {
          m.$choose_timezone_list.hide();
          m.$search_timezone_container.hide();
          m.$out_focus.trigger("focus");
          m.$choose_timezone.removeClass("enabled");
        }
        else {
          m.$choose_timezone_list.show();
          m.$search_timezone_container.show();
          m.$search_timezone_container.css({ zIndex: ++m.zIndex });
          m.$search_timezone.trigger("focus");
          m.$choose_timezone.addClass("enabled");
        }
      });
    })();
    m.toggleNewReco = function () {
      if (m.$new_reco.is(":visible")) {
        m.$error.scrollTop(m.$error[0].scrollHeight);
        m.$button_new_reco.removeClass("enabled");
        m.$input_uri[0].disabled = true;
        m.$out_focus.trigger("focus");
        m.$new_reco.hide();
        m.newRecoOn = false;
        if (!m.initialOpen && !m.doNotPushHistory) {
          window.history.pushState(
            { cat: m.currentCat, mode: m.recoMode, gotoCatsOn: m.gotoCatsOn, goOn: m.goOn, ToRsOn: m.ToRsOn, newRecoOn: m.newRecoOn },
            "", m.pathOfCat(m.currentCat, m.recoMode, null, m.hashURI, m.args)
          );
        }
        m.doNotPushHistory = false;
        m.$window.off("scroll.stop");
      }
      else {
        m.$button_new_reco.addClass("enabled");
        m.$input_uri[0].disabled = false;
        m.$input_uri.on("input.change keyup.change keydown.change cut.change paste.change click.change focus.change", m.input_uriOn);
        m.$new_reco.show();
        m.newRecoOn = true;
        if (!m.initialOpen && !m.doNotPushHistory) {
          window.history.pushState(
            { cat: m.currentCat, mode: m.recoMode, gotoCatsOn: m.gotoCatsOn, goOn: m.goOn, ToRsOn: m.ToRsOn, newRecoOn: m.newRecoOn },
            "", m.pathOfCat(m.currentCat, m.recoMode, null, m.hashURI, m.args)
          );
        }
        m.doNotPushHistory = false;
        m.$error.scrollTop(m.$error[0].scrollHeight);
        let cat = m.currentCat;
        if ( cat && (m.$input_cats.val() === "" || m.$input_title.val() === "" || m.$input_uri.val() === "") ) {
          m.$input_cats.val(cat);
        }
        m.wST = m.$window.scrollTop();
        m.$window.off("scroll.stop");
        m.$window.on("scroll.stop", function (e) {
          m.$window.scrollTop(m.wST);
        });
        m.$input_uri.trigger("focus.change");
        m.$input_uri.trigger("keyup.change");
      }
    };

