    m.$time_limit_scroll = $("#time-limit-scroll");

    m.TL = 0;
    m.lastTLIndex = 0;
    m.TLIndex = 0;
    m.TLinSec = {
      i0: Infinity,
      i1: 10 * 365 * 24 * 60 * 60,
      i2: 365 * 24 * 60 * 60,
      i3: 31 * 24 * 60 * 60,
      i4: 7 * 24 * 60 * 60,
      i5: 24 * 60 * 60,
    };
    m.TLw = m.$time_limit.width();
    m.TLwStep = m.TLw / 6.0;
    m.TLwMax = m.TLwStep * 5.0;
    m.mmInTL = function (e) {
      window.getSelection().removeAllRanges();
      e.preventDefault();
      e.stopPropagation();
      clearTimeout(m.timeoutTL);
      let x =
        e.type === "touchmove"
          ? e.originalEvent.touches[0].clientX
          : e.clientX;
      let w = x - m.$time_limit.offset().left + m.$window.scrollLeft();
      w = w < 0 ? 0 : w;
      w = w > m.TLwMax ? m.TLwMax : w;
      let changed = false;
      if (m.TL !== w) {
        m.TL = w;
        m.$time_limit_range.css({ left: m.TL });
        m.$time_limit_scroll.css({ left: m.TL - 18 });
      }
    };
    m.muInTL = function (e) {
      m.TLIndex = Math.round(m.TL / m.TLwStep);
      m.$time_limit_range.css({ left: m.TLIndex * m.TLwStep });
      m.$time_limit_scroll.css({ left: m.TLIndex * m.TLwStep - 18 });
      if (m.lastTLIndex !== m.TLIndex) {
        m.timeoutTL = setTimeout(function () {
          m.$button_get_recoms.removeClass("enabled");
          m.$window.trigger({ type: "keydown", code: "KeyR" });
        }, 2 * m.wait);
      }
    };
    m.$time_limit_scroll.on("mousedown.mdPRL touchstart.mdPRL", function (e0) {
      m.$html.on("mousemove.mdInPRL touchmove.mdInPRL", function (e1) {
        m.mmInTL(e1);
      });
      m.$html.on("mouseup.mdInPRL touchend.mdInPRL", function (e2) {
        m.muInTL(e2);
        m.$html.off(
          "mouseup.mdInPRL touchend.mdInPRL mousemove.mdInPRL touchmove.mdInPRL"
        );
      });
    });

    m.$point_range = $(".point-range").eq(0);
    m.$point_range_bar = $(".point-range-bar").eq(0);
    m.$point_range_left = $(".point-range-left").eq(0);
    m.$point_range_right = $(".point-range-right").eq(0);
    m.$point_range1 = $(".point-range").eq(1);
      // in floating-key
    m.$point_range_bar1 = $(".point-range-bar").eq(1);
    m.$point_range_left1 = $(".point-range-left").eq(1);
    m.$point_range_right1 = $(".point-range-right").eq(1);
    m.$point_range_result = $(".point-range-result");

    m.$user_noti.html(m.userId + "'s recos");
