    m.doFSCat = function (fs) {
      return new Promise(async function (resolve, reject) {
        let catsTxt = fs.$fs[0].value;
        let sEnd = fs.$fs[0].selectionEnd;
        fs.catsSplit = catsTxt.split(";");
        let l = catsTxt.length;
        fs.k = 0;
        for (let i = fs.catsSplit.length - 1; i >= 0; i--) {
          l -= fs.catsSplit[i].length + 1;
          if (l < sEnd) {
            fs.k = i;
            break;
          }
        } // Picking cat index k of the current selectionEnd.
        let fsCatSH = m.splitHangul(fs.catsSplit[fs.k].trim());
        let fsRes = await m.fuzzySearch(fsCatSH, fs);
        let str = "";
        if (fs === m.fsGotoCats) {
          for (let i = 0; i < fsRes.length; i++) {
            let fsSortedI = fsRes[fsRes.sorted[i]];
            let k = fsSortedI.i;
            let fsFLk = fs.fullList[k];
            fsFLk = fs.fullList[k] = fs.fullList[fsFLk?.cat];
            str += `<div class="list-item${fsFLk?.cat === m.currentCat ? " selected" : ""}${fsFLk?.down ? " down" : ""}${fsFLk?.deleted ? " deleted" : ""}" id="gotoCats-${k}"><span class="list-index-id">${m.escapeOnlyTag(fsFLk.cat)}</span>${fs.fullList[fsSortedI.i].html}${fsSortedI.highlight !== undefined ? `<div class="highlighted"><span class="maxMatchScore">${fsSortedI.maxMatchScore}</span> :: ${fsSortedI.highlight}</div>` : ""}</div>`;
            let $catI = $(`#cat-${m.escapeEncodePctg(encodeURIComponent(fsFLk?.cat))}`);
            if ($catI.length) {
              if (fsFLk?.down) {
                $catI.find(".baseCat").addClass("down");
              }
              if (fsFLk?.deleted) {
                $catI.find(".baseCat").addClass("deleted");
              }
            }
          }
        }
        else if (fs === m.fsCat) {
          for (let i = 0; i < fsRes.length; i++) {
            let fsSortedI = fsRes[fsRes.sorted[i]];
            str += `<div class="list-item" onclick="m.completeCat(event)"><span class="list-index-id">${m.escapeOnlyTag(fs.fullList[fsSortedI.i].cat)}</span>${fs.fullList[fsSortedI.i].html}${fsSortedI.highlight !== undefined ? `<div class="highlighted"><span class="maxMatchScore">${fsSortedI.maxMatchScore}</span> :: ${fsSortedI.highlight}</div>` : ""}</div>`;
          }
        }
        else if (fs === m.fsMRCat) {
          for (let i = 0; i < fsRes.length; i++) {
            let fsSortedI = fsRes[fsRes.sorted[i]];
            str += `<div class="list-item"><span class="list-index-id">${m.escapeOnlyTag(fs.fullList[fsSortedI.i].cat)}</span>${fs.fullList[fsSortedI.i].html}${fsSortedI.highlight !== undefined ? `<div class="highlighted"><span class="maxMatchScore">${fsSortedI.maxMatchScore}</span> :: ${fsSortedI.highlight}</div>` : ""}</div>`;
          }
        }
        fs.$fsl.html(str);
        fs.$fsLis = fs.$fsl.find(".list-item");
        if (fs === m.fsGotoCats) {
          for (let i = 0; i < fsRes.length; i++) {
            let fsSortedI = fsRes[fsRes.sorted[i]];
            let fsFLk = fs.fullList[fsSortedI.i];
            let $gotoCatI = $(`#gotoCats-${fsFLk.i}`);
            if ($gotoCatI.length) {
              if (fsFLk?.down) {
                $gotoCatI.addClass("down");
              }
            }
          }
        }
        resolve(str);
      });
    };
