    m.completeCatAndOpenCat = async function (event) {
      return new Promise(async (resolve, reject) => {
        let $elem = $(event.target);
        if (!$elem.hasClass("list-item")) {
          $elem = $elem.parents(".list-item");
        }
        if (!$elem.length) {
          reject(`Out of target which does NOT have parents $(".list-item").`);
        }
        let cat = m.currentCat = m.unescapeHTML($elem.find(".list-index-id").html());
        let fs2 = m.fsGotoCats;
        let fsGotoCatsFLCat = fs2.fullList[cat];
        if (!fsGotoCatsFLCat) {
          // Must be there.
          let i = fs2.fullList.length;
          fsGotoCatsFLCat = fs2.fullList[i] = fs2.fullList[cat] = { cat, i, txt: m.splitHangul(cat), html: m.escapeOnlyTag(cat) };
        }
        let $catI = $(`#cat-${m.escapeEncodePctg(encodeURIComponent(cat))}`);
        if ($catI.length) {
          m.$catList.find(".cat").removeClass("selected");
          $catI.addClass("selected");
          if ( m.recoMode === "" || m.recoMode === "multireco" || m.recoMode === "daily-mix" ) {
            $catI.find(".baseCat").addClass("down");
          }
        }
        if ( m.recoMode === "" || m.recoMode === "multireco" || m.recoMode === "daily-mix" ) {
          fsGotoCatsFLCat.down = true;
          $elem.addClass("down");
        }
        m.$goto_cats.trigger("focus");
        for (let i = 0; i < fs2.fullList.length; i++) {
          fs2.fullList[i].selected = false;
        }
        fsGotoCatsFLCat.selected = true;
        fs2.$fsLis = fs2.$fsl.find(".list-item");
        fs2.$fsLis.removeClass("selected");
        $elem.addClass("selected");
        m.$button_mode.removeClass("enabled");
        switch (m.recoMode) {
          case "":
            if (m.lastRecoMode === "") {
              console.log(`m.openCat("${cat}");`);
              m.openCat(cat);
            }
            else if (m.lastRecoMode === "multireco") {
              m.$contents.removeClass("multireco");
              m.$multireco_cats_container.hide();
            }
            else if (m.lastRecoMode === "neighbors" || m.lastRecoMode === "recoms") {
              console.log(`m.openCat("${cat}");`);
              m.openCat(cat);
            }
            else if (m.lastRecoMode === "daily-mix") {
              await m.refreshFSToRs(m.currentCat);
            }
            m.lastRecoMode = m.recoMode;
            m.$window.trigger({ type: "keydown", code: "KeyZ" });
            if (!m.initialOpen || !m.initialHashURI) {
              window.location.href = `#playlist-container`;
              if (m.fsToRs.fixed) {
                m.$window.scrollTop(m.$playlist_container.offset().top - m.fsToRs.$playing.outerHeight());
              }
              else {
                m.$window.scrollTop(m.$playlist_container.offset().top);
              }
            }
            break;
          case "multireco":
            m.$button_multireco_mode.addClass("enabled");
            m.$contents.addClass("multireco");
            m.$multireco_cats_container.show();
            if ( !m.catsContainsAllAnotCats( m.$multireco_input_cats[0].value, m.currentCat ) ) {
              m.$multireco_input_cats[0].value = m.currentCat;
            }
            if (m.lastRecoMode === "") {
            }
            else if (m.lastRecoMode === "multireco" || m.lastRecoMode === "neighbors" || m.lastRecoMode === "recoms") {
              console.log(`m.openCat("${cat}");`);
              m.openCat(cat);
            }
            else if (m.lastRecoMode === "daily-mix") {
              m.refreshFSToRs();
            }
            m.lastRecoMode = m.recoMode;
            m.$window.trigger({ type: "keydown", code: "KeyX" });
            break;
          case "neighbors":
            m.$button_neighbors_mode.addClass("enabled");
            console.log(`m.openCat("${cat}");`);
            m.openCat(cat);
            m.$window.trigger({ type: "keydown", code: "KeyX" });
            break;
          case "recoms":
            m.$button_get_recoms.addClass("enabled");
            console.log(`m.openCat("${cat}");`);
            m.openCat(cat);
            if (!m.initialOpen || !m.initialHashURI) {
              m.goDirectlyToHash("button-get-recoms");
            }
            break;
          case "daily-mix":
            m.$button_daily_mix.addClass("enabled");
            console.log(`m.openCat("${cat}");`);
            m.openCat(cat);
            m.$window.trigger({ type: "keydown", code: "KeyZ" });
            break;
        }
        m.fsScrollToSelected(fs2, $elem);
        if (!m.initialOpen && !m.doNotPushHistory) {
          window.history.pushState(
            { cat: m.currentCat, mode: m.recoMode, gotoCatsOn: m.gotoCatsOn, goOn: m.goOn, ToRsOn: m.ToRsOn, newRecoOn: m.newRecoOn },
            "", m.pathOfCat(m.currentCat, m.recoMode, null, m.hashURI, m.args)
          );
        }
        m.doNotPushHistory = false;
      });
    };
    m.triggerOpenCat = function (event) {
      return new Promise((resolve, reject) => {
        let $elem = $(event.target);
        if (!($elem.hasClass("baseCat") || $elem.hasClass("move-cat"))) {
          $elem = $elem.parents(".baseCat, .change-cat");
        }
        if (!$elem.length) {
          return; // * Normal activities: reject("$(event.target) is NOT [.baseCat] or [.change-cat].");
        }
        let cat = m.unescapeHTML($elem.find(".list-index-id").html());
        if (event?.which === 2) {
            // wheel click
          event.preventDefault();
          event.stopPropagation();
          window.open(
            m.pathOfCat(cat, m.recoMode, null, m.hashURI, m.args),
            "_blank"
          );
          resolve(false);
        }
        else if (event?.which === 1) {
            // left normal click
          event.preventDefault();
          event.stopPropagation();
          if ( cat !== m.currentCat || m.recoMode === "recoms" || m.recoMode === "neighbors" ) {
            m.recosShown = false;
            m.fsToRs.shuffled = null;
          }
          m.doNotPushHistory = false;
          const fs2 = m.fsGotoCats;
          let fsFLCat = fs2.fullList[cat];
          if (!fsFLCat) {
            let i = fs2.fullList.length;
            fsFLCat =
              fs2.fullList[i] =
              fs2.fullList[cat] =
                { i, cat, txt: m.splitHangul(cat), html: m.escapeOnlyTag(cat) };
          }
          let k = fsFLCat.i;
          let $gotoCatsK = $(`#gotoCats-${k}`);
          if ($gotoCatsK.length) {
            $gotoCatsK.trigger("click");
            resolve(false);
          }
          else {
            try {
              (function () {
                return new Promise((resolve, reject) => {
                  fs2.$fsl.prepend(
                    `<div class="list-item${fsFLCat?.down ? " down" : ""}${fsFLCat?.deleted ? " deleted" : ""}" id="gotoCats-${k}"><span class="list-index-id">${m.escapeOnlyTag(cat)}</span><span class="list-index">${k}</span>${fs2.fullList[k].html}</div>`
                  );
                  resolve();
                });
              })()
              .catch((err) => {
                console.error(err);
              })
              .finally(() => {
                $gotoCatsK = $(`#gotoCats-${k}`);
                $gotoCatsK.trigger("click");
                resolve(false);
              });
            }
            catch (err) {
              console.error(err);
            }
          }
        }
        resolve(false);
      });
    };

    m.$fuzzy_search.on("keydown.fs", function (e) {
      m.onKeydownInFS0(e, "G", m.fsGo);
    });
    m.showAndScrollTo = function ($recoElem) {
      if ($recoElem?.length) {
        if (!$recoElem.is(":visible")) {
          $recoElem.removeClass("none");
          $recoElem.show();
        }
        let hIID = $recoElem[0].id;
        m.hashURI = "";
        if ($recoElem.hasClass("reco")) {
          m.hashURI = m.unescapeHTML($recoElem.find(".textURI").html());
        }
        else if (hIID) {
          m.hashURI = hIID;
        }
        if (m.hashURI) {
          window.location.hash = `#${encodeURIComponent(m.hashURI)}`;
          window.history.replaceState(
            { cat: m.currentCat, mode: m.recoMode, gotoCatsOn: m.gotoCatsOn, goOn: m.goOn, ToRsOn: m.ToRsOn, newRecoOn: m.newRecoOn },
            "", m.pathOfCat(m.currentCat, m.recoMode, null, m.hashURI, m.args)
          );
        }
        m.$window.scrollTop($recoElem.offset().top);
      }
    };
    m.gotoLi = async function (event) {
      clearTimeout(m.setTimeoutGotoLi);
      if (event?.srcElement?.nodeName === "A") {
        return;
      }
      let fs = m.fsGo;
      let fs1 = m.fsToRs;
      let $elem = $(event.target);
      if (!$elem.hasClass("list-item")) {
        $elem = $elem.parents(".list-item");
      }
      console.log(
        `m.gotoLi(event) :: $elem.find(".list-index-id").html(): `,
        $elem.find(".list-index-id").html()
      );
      let k = Number($elem.find(".list-index").html());
      let uri = m.unescapeHTML($elem.find(".list-index-id").html());
      for (let i = 0; i < fs.fullList.length; i++) {
        fs.fullList[i].selected = false;
      }
      fs.fullList[uri].selected = true;
      let fsFLUri = fs.fullList[uri];
      if (!m.initialOpen && $elem.hasClass("selected")) {
        if ($elem.hasClass("clicked-twice")) {
          fs.$fs.trigger({ type: "keydown", code: "Escape" });
          fs.$fsl.find(".list-item").removeClass("clicked-twice");
        }
        else {
          $elem.addClass("clicked-twice");
        }
      }
      else {
        fs.$fsLis = fs.$fsl.find(".list-item");
        fs.$fsLis.removeClass("selected");
        $elem.addClass("selected");
      }
      m.beInCurrentCat = false;
      let catsSplit = fsFLUri.catsSplit = m.catsToString(fsFLUri.r?.cats).split(";");
      for (let i = catsSplit.length - 1; i >= 0; i--) {
        if (m.currentCat === catsSplit[i]) {
          m.beInCurrentCat = true;
          break;
        }
      }
      if (!m.beInCurrentCat) {
        m.currentCat = catsSplit[0];
        m.recoMode = "";
        try {
          await m.openCat(m.currentCat);
        }
        catch (err) {
          console.error(err);
        }
      }
      let l = fsFLUri?.catAndI?.[m.currentCat]?.i ?? fs1.fullList.length - 1;
      if (m.beInCurrentCat && l !== k) {
        console.log(`l:${l} and k:${k} are different! This is problematic.`);
      }
      let $recoElem = $(`#reco-${l}`);
      if ($recoElem.length) {
        m.showAndScrollTo($recoElem);
      }
      else if (isNaN(l)) {
        try {
          (function () {
            return new Promise(async (resolve) => {
              m.$contents_recos.prepend(await m.recoHTML(m.$recoDeleted[uri]?.r));
              resolve();
            });
          })()
          .catch((err) => {
            console.error(err);
          })
          .finally(() => {
            $recoElem = $(`#reco-${l}`);
            if ($recoElem.length) {
              m.showAndScrollTo($recoElem);
            }
          });
        }
        catch (err) {
          console.error(err);
        }
      }
      else {
        let i = Math.floor((m.catUriList[m.currentCat].uris.length - 1 - l) / m.maxShowReco);
        try {
          (function () {
            return new Promise(async (resolve) => {
              await $(`#load-recos-${i}`).trigger("click");
              resolve();
            });
          })()
          .catch((err) => {
            console.error(err);
          })
          .finally(() => {
            let $recoElem = $(`#reco-${l}`);
            if ($recoElem.length) {
              m.showAndScrollTo($recoElem);
              console.log(`m.showAndScrollTo($(\`#reco-${l}\`));`);
            }
            else {
              if (!m.setTimeoutGotoLiCount || m.setTimeoutGotoLiCount >= 3) {
                m.setTimeoutGotoLiCount = 0;
              }
              m.setTimeoutGotoLiCount++;
              if (m.setTimeoutGotoLiCount < 3) {
                m.setTimeoutGotoLi = setTimeout(function () {
                  m.gotoLi(event);
                }, m.wait);
              }
            }
          });
        }
        catch (err) {
          console.error(err);
        }
      }
    };
    m.doFSGo = async function () {
      let fs = m.fsGo;
      if (fs.shuffledOnce) {
        // Because shuffledOnce. But when only saved???
        let cat = m.currentCat;
        if (!m.catUriList[cat]?.has) {
          await m.getUriList("cat\n" + (cat === "" ? "\tp" : cat));
        }
        let uris = m.catUriList[cat].uris;
        let fsFL = fs.fullList;
        for (let k = 0; k < uris.length; k++) {
          let uri = String(uris[k]);
          if (m.catUriList[cat]?.uris?.[uri]) {
            m.catUriList[cat].uris[uri].i = k;
          }
          if (!fsFL[uri]) {
            let r = m.userRecos[uri];
            fsFL[uri] = { uri, r, i: m.fsGo.fullList.length, catsSplit: m.catsToString(r?.cats).split(";"), txt: m.splitHangul(`${r?.cats} :: ${r?.title}`), html: m.escapeOnlyTag(r?.title), catAndI: {} };
          }
          if (!fsFL[uri].catAndI) {
            fsFL[uri].catAndI = {};
          }
          fsFL[uri].catAndI[cat] = { cat, i: k };
        }
      }
      let fsPtnSH = m.splitHangul(fs.$fs[0].value);
      let fsRes = await m.fuzzySearch(fsPtnSH, fs);
      let sorted = fsRes.sorted;
      let str = "";
      for (let i = 0; i < sorted.length; i++) {
        let k = fsRes[sorted[i]].i;
        let fsFLk = fs.fullList[k];
        if (fsFLk) {
          str += `<div id="li-${k}" class="list-item${fsFLk.selected ? " selected" : ""}${fsFLk.r?.deleted ? " deleted" : ""}"><span class="list-index">${k}</span><span class="list-index-id">${m.escapeOnlyTag(fsFLk.r?.uri)}</span>${fsFLk.html}${fsRes[sorted[i]].highlight !== undefined ? `<div class="highlighted"><span class="maxMatchScore">${fsRes[sorted[i]].maxMatchScore}</span> :: ${fsRes[sorted[i]].highlight}</div>` : ""}</div>`;
        }
      }
      fs.$fsl.html(str);
      fs.$fsLis = fs.$fsl.find(".list-item");
      let $selected = fs.$fsLis.filter(".selected");
      if ($selected.length) {
        fs.$fsl.scrollTop($selected.offset().top - fs.$fsl.offset().top - 52);
      }
      else {
        fs.$fsl.scrollTop(0);
      }
    };
    m.fsGoOn = function () {
      return new Promise(async (resolve, reject) => {
        let now = Date.now();
        let passed = now - m.previous;
        if (passed > m.wait) {
          m.previous = now;
          try {
            await m.doFSGo();
          }
          catch (err) {
            console.error(err);
          }
          resolve();
        }
        else {
          m.$fuzzy_search.off("keyup.fs cut.fs paste.fs click.fs");
          setTimeout(async function () {
            m.$fuzzy_search.on("keyup.fs cut.fs paste.fs click.fs", m.fsGoOn);
            m.previous = Date.now();
            try {
              await m.doFSGo();
            }
            catch (err) {
              console.error(err);
            }
            resolve();
          }, m.wait * 1.5 - passed);
        }
      });
    };

    m.$table_of_recos.on("keydown", function (e) {
      m.onKeydownInFS0(e, "T", m.fsToRs);
    });
    m.$button_save_this_order = $("#button-save-this-order");
    m.$result_of_change_order = $("#result-of-change-order");
    if (m.myPage) {
      m.$button_save_this_order.show();
      m.$button_save_this_order.on("click", function (e) {
        return new Promise((resolve, reject) => {
          m.$result_of_change_order.html("[--Changing the order of UriList.--]");
          m.$numbers_of_recos.html("[--Changing the order of UriList.--]");
          let uris = "";
          let fs = m.fsToRs;
          for (let i = 0; i < fs.shuffled.length; i++) {
            // Newest has the maximum index i. Newest uri to the bottom (push).
            uris += "\n" + fs.fullList[fs.shuffled[i].i]?.r?.uri;
          }
          uris = uris.trim();
          if (m.catUriList[m.currentCat].UriList.trim() === uris) {
            m.$result_of_change_order.html("[--UriList order is the same as before.--]");
            m.$numbers_of_recos.html("[--UriList order is the same as before.--]");
            reject("[--UriList order is the same as before.--]");
          }
          else {
            let urisReversed = uris.split("\n").reverse().join("\n");
              // Reverse is needed.
            m.rmb_me(function () {
              $.ajax({
                type: "POST",
                url: `${m.userPath}/change-order-of-UriList?cat=${encodeURIComponent(m.currentCat)}`,
                data: urisReversed,
                dataType: "text",
              })
              .fail(function (resp) {
                m.$result_of_change_order.html(resp);
                reject(resp);
              })
              .done(async function (resp) {
                if (resp === "true") {
                  m.$result_of_change_order.html("[--UriList order is changed.--]");
                  m.$numbers_of_recos.html("[--UriList order is changed.--]");
                  m.catUriList[m.currentCat].UriList = uris;
                  m.catUriList[m.currentCat].uris.splice(0, m.catUriList[m.currentCat].uris.length);
                    // To preserve uris[uri]={cat, i}.
                  let urisSplit = uris.split("\n");
                  for (let i = 0; i < urisSplit.length; i++) {
                    let uri = urisSplit[i];
                    m.catUriList[m.currentCat].uris[i] = uri;
                    m.catUriList[m.currentCat].uris[uri].i = i;
                  }
                  fs.shuffled = null;
                  fs.shuffledOnce = true;
                  m.fsGo.shuffledOnce = true;
                  m.lastRecoMode = "nothing";
                  m.doNotPushHistory = true;
                  m.reTriggerFS(fs);
                  m.reTriggerFS(m.fsGo);
                  fs.shuffledOnce = true;
                  m.fsGo.shuffledOnce = true;
                  m.openCat(m.currentCat);
                  resolve();
                }
                else if (resp === "false") {
                  m.$result_of_change_order.html("[--UriList order is NOT changed. Some error may occur.--]");
                  m.$numbers_of_recos.html("[--UriList order is NOT changed. Some error may occur.--]");
                  reject("[--UriList order is NOT changed. Some error may occur.--]");
                }
                else {
                  m.$result_of_change_order.html(`[--Server error.--] ${resp}`);
                  m.$numbers_of_recos.html(`[--Server error.--] ${resp}`);
                  reject(`[--Server error.--] ${resp}`);
                }
              });
            });
          }
        });
      });
    }

