import $ from 'jquery';
import m from "../../prime-src/prepare.js";
import { useEffect } from 'react';

export default function Sidebar({ userCatList, rawCatListHtML, opened, isChangingOrder, onChangeOrder }) {
  useEffect(() => {
    m.$change_catList_order = $("#change-catList-order");
    m.$change_catList_order_ok = $("#change-catList-order-ok");
    m.$change_catList_order_cancel = $("#change-catList-order-cancel");
    m.handleChangeCat = (e) => {
      try {
        m.triggerOpenCat(e);
      } catch (err) {
        // console.error(err);
        // ! clicking blank space is normal activities.
      }
      return false;
    };
  }, []);

  useEffect(() => {
    (async () => {
      m.$sidebar_dragger = $("#sidebar-dragger");
      m.$sidebar_toggle = $("#sidebar-toggle");
      m.$sidebar_dragger_in = $("#sidebar-dragger-in");
      m.$sidebar = $("#sidebar");
      m.$catList = $("#catList");
      m.$catList_result = $("#catList-result");
      m.$catList.on("mouseup.change-cat", m.handleChangeCat);
      await m.strCatListToJSON(m.unescapeHTML(userCatList), m.catList);
      let cat = m.currentCat;
      const cL = m.catList;
      while (cat = m.getSuperCat(cat)) {
        if (cL[cat]) {
          cL[cat].subCatExpanded = true;
        }
      }
      await m.catListToHTML();
        // ! catList 는 SideBar 에다 그리는 용도와 m.fsGotoCats 로만 쓰여서 FS 와 관련 없음. 그래서 m.myCatList 는 update 불필요. 단 m.myCatListHTMLEscaped 는 m.myFSCatList 를 위해 업데이트 필요!!!
      await m.strCatListToJSON(m.unescapeHTML(m.myCatListHTMLEscaped.trim()+"\n"+m.catListHTMLEscaped.trim()+"\n"+m.kipidCatListHTMLEscaped.trim()), m.myFSCatList);

      m.updateCatFS = function () {
        return new Promise(async (resolve, reject) => {
          // * m.myCatList must be updated first.
          await m.strCatListToJSON(m.unescapeHTML(userCatList), m.catList);
          await m.catListToHTML();
          m.fsGotoCats.fullList.splice(0, m.fsGotoCats.fullList.length);
          let l = m.catList.length;
          for (let i = l - 1; i > 0; i--) {
            m.putCatToFSFullList(l - 1 - i, m.catList[i].cat, m.catList);
              // ! m.fsGotoCats 에 쓰임.
          }

          await m.strCatListToJSON(m.unescapeHTML(m.myCatListHTMLEscaped.trim()+"\n"+m.catListHTMLEscaped.trim()+"\n"+m.kipidCatListHTMLEscaped.trim()), m.myFSCatList);
          m.fsCat.fullList.splice(0, m.fsCat.fullList.length);
          m.fsMRCat.fullList.splice(0, m.fsMRCat.fullList.length);
          l = m.myFSCatList.length;
          for (let i = l - 1; i > 0; i--) {
            m.putCatToFSFullList(l - 1 - i, m.myFSCatList[i].cat, m.myFSCatList);
              // ! m.fsCat 과 m.fsMRCat 에 쓰임.
          }

          let cat = "";
          if (!m.fsCat.fullList[cat]) {
            m.fsCat.fullList[cat] = { i: m.fsCat.fullList.length, txt: m.splitHangul("[--Uncategorized--]"), cat, html: m.escapeOnlyTag("[--Uncategorized--]") };
            m.fsCat.fullList.push(m.fsCat.fullList[cat]);
          }
          if (!m.fsMRCat.fullList[cat]) {
            m.fsMRCat.fullList[cat] = { i: m.fsMRCat.fullList.length, txt: m.splitHangul("[--Uncategorized--]"), cat, html: m.escapeOnlyTag("[--Uncategorized--]") };
            m.fsMRCat.fullList.push(m.fsMRCat.fullList[cat]);
          }
          if (m.myPage) {
            if (!m.fsGotoCats.fullList[cat]) {
              m.fsGotoCats.fullList[cat] = { i: m.fsGotoCats.fullList.length, txt: m.splitHangul("[--Uncategorized--]"), cat, html: m.escapeOnlyTag("[--Uncategorized--]") };
              m.fsGotoCats.fullList.push(m.fsGotoCats.fullList[cat]);
            }
          }
          resolve();
        });
      };
      await m.updateCatFS();
      m.updateGotoCatsFS = function () {
        return new Promise(async (resolve, reject) => {
          await m.strCatListToJSON(m.unescapeHTML(userCatList), m.catList);
          m.fsGotoCats.fullList.splice(0, m.fsGotoCats.fullList.length);
          let l = m.catList.length;
          for (let i = l - 1; i > 0; i--) {
            m.putCatToFSFullList(l - 1 - i, m.catList[i].cat, m.catList);
          }
          let i = l - 1;
          let cat = "";
          let fsFLCat = m.fsGotoCats.fullList[cat];
          if (fsFLCat) {
            m.fsGotoCats.fullList[i] = m.fsGotoCats.fullList[cat] = { ...fsFLCat, i, txt: m.splitHangul("[--Uncategorized--]"), cat, html: m.escapeOnlyTag("[--Uncategorized--]") };
          }
          else {
            fsFLCat =
              m.fsGotoCats.fullList[i] =
              m.fsGotoCats.fullList[cat] = { i, txt: m.splitHangul("[--Uncategorized--]"), cat, html: m.escapeOnlyTag("[--Uncategorized--]") };
          }
          resolve();
        });
      };
      await m.updateGotoCatsFS();

      m.$sidebar_dragger_in.on("mousedown.sd touchstart.sd", function (e) {
        let touch0 = e.type === "touchstart" ? e.originalEvent.touches[0] : e;
        let relativeX = touch0.clientX - Math.round(m.$sidebar_dragger.offset().left) + m.$window.scrollLeft();
        m.$html.on("mousemove.sd touchmove.sd", function (e) {
          window.getSelection().removeAllRanges();
          e.preventDefault();
          e.stopPropagation();
          let touch0 = e.type === "touchmove" ? e.originalEvent.touches[0] : e;
          m.$sidebar.css({
            marginLeft: -m.$sidebar.outerWidth() + touch0.clientX - relativeX,
          });
          m.$sidebar_dragger.css({ left: touch0.clientX - relativeX });
          m.$html.on("mouseup.sd touchend.sd", function () {
            m.$html.off("mouseup.sd touchend.sd mousemove.sd touchmove.sd");
            if (touch0.clientX - relativeX > m.$sidebar.outerWidth() / 2) {
              m.$sidebar.css({ marginLeft: 0 });
              m.$sidebar_dragger.css({ left: m.$sidebar.outerWidth() });
              updateOpened(draft => { draft.value = true; draft.count = draft.count+1; });
            } else {
              m.$sidebar.css({ marginLeft: -m.$sidebar.outerWidth() });
              m.$sidebar_dragger.css({ left: 0 });
              updateOpened(draft => { draft.value = false; draft.count = draft.count+1; });
            }
          });
        });
      });

      try {
        m.$change_catList_order_cancel.trigger("click.change-order-catList");
      } catch (err) {
        console.error(err);
      }
    })();
  }, [userCatList]);

  return (
    <aside id="sidebar" count={opened.count}>
      <div id="user-noti"></div>
      <div id="catList" dangerouslySetInnerHTML={{ __html: rawCatListHtML }}></div>
      <div id="catList-result"></div>
      <div className="right">
        <div className="button" style={{display: !isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order" onClick={() => {
          onChangeOrder(true);
        }}>[--Change orders--]</div>
        <div className="button" style={{display: isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order-ok" onClick={() => {
          onChangeOrder(false);
        }}>[--OK--]</div>
        <div className="button" style={{display: isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order-cancel" onClick={() => {
          onChangeOrder(false);
        }}>[--Cancel--]</div>
      </div>
    </aside>
  );
}